@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,900&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  font-family: "Montserrat", sans-serif;
  /* background-color: #0A192F; */
}

/* Firefox */
* {
  scrollbar-color: #f24432 #2a1b3f;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 3px;
}

*::-webkit-scrollbar-track {
  background: #2a1b3f;
}

*::-webkit-scrollbar-thumb {
  background-color: #2a1b3f;
  border-radius: 7px;
}
.img-wraper {
  overflow: hidden;
}
.devimg {
  transition: scale 400ms ease-in-out;
  background: hsla(168, 61%, 58%, 1);

  background: radial-gradient(
    circle,
    hsla(168, 61%, 58%, 1) 0%,
    hsla(168, 100%, 83%, 1) 100%
  );

  background: -moz-radial-gradient(
    circle,
    hsla(168, 61%, 58%, 1) 0%,
    hsla(168, 100%, 83%, 1) 100%
  );

  background: -webkit-radial-gradient(
    circle,
    hsla(168, 61%, 58%, 1) 0%,
    hsla(168, 100%, 83%, 1) 100%
  );

  filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#54D6BB", endColorstr="#A6FFED", GradientType=1 );
  border-radius: 66% 34% 31% 69% / 39% 50% 50% 61%;
}

.img {
  transition: scale 400ms ease-in-out;
}
.img:hover,
.devimg:hover {
  scale: 105%;
  cursor: pointer;
}

i:hover {
  color: #f24432;
  cursor: pointer;
}

.loader {
  width: 50px;
  height: 50px;

  animation: heartbeat 2s infinite;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
    opacity: 0.3;
  }

  25% {
    transform: scale(1.2);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.4);
    opacity: 1;
  }

  100% {
    transform: scale(1);
    opacity: 0.3;
  }
}
.ant-tabs-tab-btn {
  color: #353535 !important;
}

.ant-tabs-tab-active {
  font-weight: 500 !important;
}
.admin-img {
  height: 320px;
  width: 300px;
  background: hsla(265, 56%, 67%, 1);

  background: linear-gradient(
    225deg,
    hsla(265, 56%, 67%, 1) 0%,
    hsla(265, 40%, 18%, 1) 100%
  );

  background: -moz-linear-gradient(
    225deg,
    hsla(265, 56%, 67%, 1) 0%,
    hsla(265, 40%, 18%, 1) 100%
  );

  background: -webkit-linear-gradient(
    225deg,
    hsla(265, 56%, 67%, 1) 0%,
    hsla(265, 40%, 18%, 1) 100%
  );

  filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#A27ADA", endColorstr="#2A1B3F", GradientType=1 );
}

.card:hover {
  background: #f24432;
  transform: scale(1.05);
}
.card:hover .count {
  color: #ffffff;
  font-weight: bold;
}

.cardProject {
  width: 30%;
  /* min-height: 500px; */

  overflow: hidden;
  border-radius: 10px;
  transition: ease-in-out;
}
.cardProject:hover {
  background: rgb(201, 209, 218);
  cursor: pointer;
}
.cardExperience {
  width: 30%;

  overflow: hidden;
  border-radius: 10px;
  transition: ease-in-out;
}
.cardExperience:hover {
  background: rgb(201, 209, 218);
  cursor: pointer;
  /* transform: scale(1.02); */
}
.cardExperience:hover .title {
  color: #f24432;
}
.cardExperience:hover strong {
  color: #f24432;
}

